.head {
    margin-bottom: 40px;
}
.main{
    display: flex;
    align-items: end;
    justify-content: space-between;
    flex-wrap: wrap;
}
.search {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
}
.title{
    margin-top: 32px;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    color: #000000;
}

.emptyHead button {
    margin: 0 auto 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
}
.text {
    margin: 68px 0 32px 0;
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    opacity: 0.5;
}